@import url("https://fonts.googleapis.com/css2?family=Commissioner:wght@400;500;700;800&display=swap");
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Commissioner", sans-serif;
  font-size: 16px;
}

.pad {
  padding: 0 30px;
}
@media (min-width: 800px) {
  .pad {
    padding: 0 100px;
  }
}

.hide {
  display: none;
}

.Nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: auto;
  position: relative;
}
.Nav-bar .logo {
  font-size: 2.3rem;
  color: white;
  font-weight: 800;
}
@media (min-width: 800px) {
  .Nav-bar .logo {
    font-size: 1.5rem;
  }
}
.Nav-bar .list-items {
  flex-direction: column;
  align-items: flex-start;
  background-color: white;
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  top: 120%;
  width: 350px;
  padding: 40px 0;
  height: 25vh;
  margin: 0 auto;
  z-index: 4;
  overflow: hidden;
}
.Nav-bar .list-items li {
  list-style: none;
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 65px;
  position: relative;
}
.Nav-bar .list-items li:after {
  content: "";
  background-color: rgb(222, 213, 213);
  height: 1px;
  width: 450px;
  position: absolute;
  left: 0;
  top: 82px;
}
.Nav-bar .list-items a {
  text-decoration: none;
  color: black;
  padding: 0 40px;
}
@media (min-width: 800px) {
  .Nav-bar .list-items {
    display: flex;
  }
  .Nav-bar .list-items li {
    list-style: none;
    margin-right: 30px;
    font-size: 0.9rem;
  }
  .Nav-bar .list-items a {
    text-decoration: none;
    color: white;
  }
}
.Nav-bar .mobile-menu img {
  width: 30px;
  height: 30px;
}
@media (min-width: 800px) {
  .Nav-bar .mobile-menu img {
    display: none;
  }
}/*# sourceMappingURL=Nav.css.map */